$otella-blue : #3d71c9;
$mobile: 767px;
$tablet: 768px;
$small-upper: 991px;
$tablet-max: 1080px;
$laptop: 1380px;
$medium-laptop: 1440px;
$medium-desktop: 1680px;

@mixin background-box($width, $paddingTop, $minHeight) {
	position: relative;
	width: $width;
	min-height: $minHeight;

	&:before {
		content: '';
		display: block;
		padding-top: $paddingTop;
	}
}

@mixin media-query-min($width) {
	@media screen and (min-width:$width) {
		@content;
	}
}

@mixin media-query-max($width) {
	@media screen and (max-width:$width) {
		@content;
	}
}

@mixin media-query-mobile {
	@media screen and (max-width:$mobile) {
		@content;
	}
}

@mixin media-query-tablet-portrait {
	@media screen and (max-width:$tablet) and (orientation: portrait) {
		@content;
	}
}

@mixin media-query-small-upper {
	@media screen and (max-width:$small-upper) {
		@content;
	}
}

@mixin media-query-tablet-max {
	@media screen and (max-width:$tablet-max) {
		@content;
	}
}

@mixin media-query-max-1280 {
	@media screen and (max-width:1280px) {
		@content;
	}
}

@mixin media-query-laptop {
	@media screen and (max-width:$laptop) {
		@content;
	}
}

@mixin media-query-medium-laptop {
	@media screen and (max-width:$medium-laptop) {
		@content;
	}
}

@mixin media-query-medium-desktop {
	@media screen and (max-width:$medium-desktop) {
		@content;
	}
}

@mixin background-opacity($color, $opacity: 0.3) {
	background: $color; /* The Fallback */
	background: rgba($color, $opacity);
}


