		GENERAL STYLES

   =========================*/
*, *:before, *:after {
  box-sizing: border-box;
  border: none; }

a {
	font-size: 14px;

	@include media-query-laptop {
		font-size: 13px;
	}

	@include media-query-tablet-max {
		font-size: 12px;
	}
}

a:link,
a:visited,
a:active {
	color: #fff;
	pointer-events: auto;
	text-decoration: none;
}

a:link:hover,
a:visited:hover,
