#navigation {
	position: fixed;
  	top: 0;
  	left: 0;
	right: 0;
  	width: 100%;
	max-width: 1440px;
	margin-left: auto;
	margin-right: auto;
  	z-index: 2;
	padding-top: 20px;
	padding-bottom: 20px;
	margin-top: 10px;

	@include media-query-max(1600px) {
		max-width: 1400px;
	}

	@include media-query-laptop {
		max-width: 1320px;
	}

	&.white {
		.navigation-left,
		.navigation-right  {
			ul {
				> li {
					> a {
						color: #fff;
					}
				}
			}
		}
	}

	.navigation-left,
	.navigation-right {
		position: relative;
		width: 42%;
		display: inline-block;
		vertical-align: middle;

		ul {
			list-style: none;
			padding-left: 0;
			width: 100%;
			@include media-query-min($tablet){
			    margin: 0 auto;
			    display: table;
			    table-layout: fixed;
			    float: none;
			}

			> li {
				display: table-cell;
				text-align: center;

				&.active {
					> a {
						span {
							&:after {
								width: 100%;
							}
						}
					}
				}

				> a {
					position: relative;
					display: block;
					font-family: 'HKGrotesk-Medium';
				    color: #727272;
				    text-transform: uppercase;
				    font-size: 16px;
				    letter-spacing: 1px;
					transition: color 0.3s ease;

					@include media-query-max(1600px) {
						font-size: 15px;
					}

					@include media-query-laptop {
						font-size: 14px;
					}

					&:hover,
					&:focus {
						span {
							&:after {
								width: 100%;
							}
						}
					}

					span {
						position: relative;
						padding: 4px;

						&:after {
							content: '';
							display: block;
							height: 2px;
							background-color: $otella-blue;
							position: absolute;
							bottom: 0;
							left: 0;
							width: 0;
							transition: width 0.3s ease;
						}
					}
				}
			}
		}
	}

	.navigation-logo {
		position: relative;
		width: 16%;
		display: inline-block;
		vertical-align: middle;

		img.logo {
			position: relative;
			width: 100%;
			max-width: 175px;
			margin-left: auto;
			margin-right: auto;
			display: block;
			transform: translate3d(0,0,0);

			@include media-query-laptop {
				max-width: 165px;
			}
		}
	}
}

#navigation-bottom {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	height: auto;
	padding: 0 5% 25px;
	z-index: 2;
	pointer-events: none;

	@include media-query-max(1600px) {
		padding-left: 4%;
		padding-right: 4%;
	}

	@include media-query-laptop {
		padding-left: 3%;
		padding-right: 3%;
	}

	.navigation-bottom-inner {
		position: relative;
		width: 100%;

		.choose-language {
			position: relative;
			display: inline-block;
			vertical-align: middle;

			&.white {
				ul {
					> li {
						color: #fff;
					}
				}
			}

			ul {
				padding-left: 0;
				list-style: none;
				margin-bottom: 0;
				font-size: 0;
				pointer-events: auto;

				> li {
					position: relative;
					padding: 2px 8px;
					display: inline-block;
					vertical-align: middle;
					font-size: 16px;
					transition: color 0.3s ease;
					cursor: pointer;

					@include media-query-max(1600px) {
						font-size: 15px;
					}

					@include media-query-laptop {
						font-size: 14px;
					}

					&:after {
						content: '';
						display: block;
						position: absolute;
						top: 50%;
						right: 0;
						transform: translate3d(0,-50%,0);
						height: 110%;
						width: 1px;
						background-color: #727272;
					}

					&:before {
						content: '';
						display: block;
						position: absolute;
						bottom: 0;
						left: 0;
						width: 0;
						height: 2px;
						background-color: $otella-blue;
						transition: width 0.3s ease;
					}

					&:last-child {
						&:after {
							content: none;
						}
					}

					&:hover {
						&:before {
							width: 100%;
						}
					}
				}
			}
		}

		.scroll-down-icon-wrapper {
			position: absolute;
			display: inline-block;
			vertical-align: middle;
			left: 50%;
			transform: translate3d(-50%,-20%,0);
			pointer-events: auto;

			img.scroll-down-icon {
				position: relative;
				display: block;
				transform: translate3d(0,0,0);
				width: 20px;
				margin-bottom: 5px;

				@include media-query-medium-laptop {
					width: 16px;
				}
			}

			img.scroll-down-icon-arrow {
				position: relative;
				display: block;
				width: 10px;
				margin: 0 auto;
				transform: translate3d(0,0,0);

				@include media-query-medium-laptop {
					width: 8px;
				}
			}
		}

		.social-media-icon-wrapper {
			position: absolute;
			display: inline-block;
			vertical-align: middle;
			right: 0;
			top: 50%;
			transform: translate3d(0,-50%,0);

			&.grey {
				ul {
					> li {
						border-right-color: #5c5c5c;

						.fa {
							color: #5c5c5c;
						}
					}
				}
			}

			ul {
				padding-left: 0;
				list-style: none;
				font-size: 0;
				pointer-events: auto;
				margin-bottom: 0;

				> li {
					position: relative;
					display: inline-block;
					vertical-align: middle;
					padding: 2px 10px;
					border-right: 1px solid #fff;
					line-height: 1;

					&:last-child {
						border-right: none;
					}

					.fa {
						font-size: 16px;
						transition: color 0.3s ease;

						@include media-query-max(1600px) {
							font-size: 15px;
						}

						@include media-query-laptop {
							font-size: 14px;
						}
					}
				}
			}
		}
	}
}

.side-menu {
	position: absolute;
	padding: 5% 9% 5% 10%;
	padding: 25px 8%;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	background: linear-gradient(#eefbff, #c3e4ed);
	transform: translateX(-100%) skewX(-10deg);
	transform-origin: 100% 0;
	transition: transform 0.4s cubic-bezier(0.694, 0, 0.335, 1);
	z-index: 3;
	box-shadow: 10px 10px 74px -9px rgba(255,255,255,1);

	&.active {
		transform: translateX(0) skewX(-10deg);
	}

	button.close-modal {
		transform: translate3d(0,0,0);
		right: 0;
	}

	.flex {
		position: relative;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;

		.inner {
			position: relative;
			width: 100%;
			max-width: 580px;
			margin-left: auto;
			margin-right: auto;
			transform: skewX(10deg) translate3d(0,0,0);

			h2 {
				margin-top: 0;
				margin-bottom: 12%;
				text-align: center;
			}

			.form-group {
				margin-bottom: 10%;

				@include media-query-max(1600px) {
					margin-bottom: 7%;
				}

				@include media-query-max(1380px) {
					margin-bottom: 6%;
				}
			}
		}
	}
}

