button.btn-custom {
	position: relative;
    background-color: $otella-blue;
    font-size: 15px;
    color: #fff;
    border-radius: 0;
	transform-origin: 0 100%;
    padding: 10px 28px;
	border: 1px solid $otella-blue;
	transform: skewX(-20deg);
	overflow: hidden;
	transition: background-color 0.3s ease, color 0.3s ease;

	@include media-query-laptop {
		font-size: 14px;
		padding-top: 7px;
		padding-bottom: 7px;
	}

	&.normal {
		transform: none;

		.unskew {
			transform: none;
		}
	}

	&.no-border {
		border: none;
	}

	&.no-padding.inverted {
		padding-left: 0;
		padding-right: 0;
		overflow: visible;

		span.unskew {
			svg {
				overflow: visible;

				* {
					transition: all 0.3s cubic-bezier(0.694, 0, 0.335, 1);
				}
			}
		}

		&:hover,
		&:focus,
		&:active,
		&.active {
			color: #727272;
			background-color: #fff;

			span.unskew {
				svg *{
					fill: $otella-blue;
					transform: translateX(20px);
				}
			}
		}

		&:before {
			content: '';
			display: block;
		}
	}

	&.inverted {
		background-color: #fff;
		color: #727272;

		span.unskew {
			svg *{
				fill: $otella-blue;
			}
		}

		&:hover,
		&:focus,
		&:active,
		&.active {
			color: #fff;
			background-color: $otella-blue;

			span.unskew {
				svg *{
					fill: #fff;
				}
			}
		}
	}

	&.transparent {
		background-color: transparent;
		color: #fff;
	}

	&:hover,
	&:focus,
	&:active,
	&.active {
		color: #727272;
		background-color: #fff;

		span.unskew {
			svg *{
				fill: $otella-blue;
			}
		}
	}

	span.unskew {
		display: block;
		transform: skewX(20deg);

		span.text {
			display: inline-block;
			vertical-align: middle;
			margin-right: 10px;
		}

		svg {
			display: inline-block;
			vertical-align: middle;
			width: 20px;

			* {
				transition: fill 0.3s ease;
			}
		}
	}

	&.min-width {
		min-width: 130px;
	}

	&.smaller-padding {
		padding-top: 6px;
		padding-bottom: 6px;
	}
}

button.close-modal {
	position: absolute;
	top: 0;
	right: 0;
	padding: 20px 10px;
	background-color: $otella-blue;
	z-index: 2;

	&.inverted {
		background-color: #fff;

		.close-icon {
			span.stripe {
				background-color: $otella-blue;
			}
		}
	}

	.close-icon {
		position: relative;
		transform: rotateZ(45deg);

		span.stripe {
			position: relative;
			display: block;
			height: 2px;
			width: 22px;
			background-color: #fff;

			&:last-child {
				position: absolute;
				top: -10px;
				left: 10px;
				width: 2px;
				height: 22px;
			}
		}
	}
}

#news-and-gallery {
	.news-and-gallery-thumbnail {
		position: relative;
		width: 25%;
		float: left;
		height: 50vh;
		transform: skewX(-10deg);
		overflow: hidden;
		backface-visibility: initial;
		image-rendering: crisp-edges;
		transform-origin: 0 100%;

		&.bottom {
			transform: skewX(10deg);
			transform-origin: 0 0;

			.inner {
				transform: skewX(-10deg);
				transform-origin: 0 0;

				.news-and-gallery-content {
					padding: 5% 16% 5% 23%;
				}
			}
		}

