.scaleUp {
	animation-name: scaleUp;
}

@keyframes scaleUp {
	0% {
		opacity: 0;
		transform: scale3d(0,0,0);
	}
	80% {
		transform: scale3d(1.1,1.1,1.1);
		opacity: 1;
	}
	100% {
		transform: scale3d(1,1,1);
		opacity: 1;
	}
}


