/* =========================

		GENERAL STYLES

   =========================*/
*, *:before, *:after {
  box-sizing: border-box;
  border: none;
}

a {
  font-size: 14px;
}

@media screen and (max-width: 1380px) {
  a {
    font-size: 13px;
  }
}

@media screen and (max-width: 1080px) {
  a {
    font-size: 12px;
  }
}

a:link,
a:visited,
a:active {
  color: #fff;
  pointer-events: auto;
  text-decoration: none;
}

a:link:hover,
a:visited:hover,
a:active:hover {
  color: #dbdbdb;
  transition: color 0.3s;
}

button:focus, button:active {
  outline: none !important;
}

