#navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  z-index: 2;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 10px;
}

@media screen and (max-width: 1600px) {
  #navigation {
    max-width: 1400px;
  }
}

@media screen and (max-width: 1380px) {
  #navigation {
    max-width: 1320px;
  }
}

#navigation.white .navigation-left ul > li > a,
#navigation.white .navigation-right ul > li > a {
  color: #fff;
}

#navigation .navigation-left,
#navigation .navigation-right {
  position: relative;
  width: 42%;
  display: inline-block;
  vertical-align: middle;
}

#navigation .navigation-left ul,
#navigation .navigation-right ul {
  list-style: none;
  padding-left: 0;
  width: 100%;
}

@media screen and (min-width: 768px) {
  #navigation .navigation-left ul,
  #navigation .navigation-right ul {
    margin: 0 auto;
    display: table;
    table-layout: fixed;
    float: none;
  }
}

#navigation .navigation-left ul > li,
#navigation .navigation-right ul > li {
  display: table-cell;
  text-align: center;
}

#navigation .navigation-left ul > li.active > a span:after,
#navigation .navigation-right ul > li.active > a span:after {
  width: 100%;
}

#navigation .navigation-left ul > li > a,
#navigation .navigation-right ul > li > a {
  position: relative;
  display: block;
  font-family: 'HKGrotesk-Medium';
  color: #727272;
  text-transform: uppercase;
  font-size: 16px;
  letter-spacing: 1px;
  transition: color 0.3s ease;
}

@media screen and (max-width: 1600px) {
  #navigation .navigation-left ul > li > a,
  #navigation .navigation-right ul > li > a {
    font-size: 15px;
  }
}

@media screen and (max-width: 1380px) {
  #navigation .navigation-left ul > li > a,
  #navigation .navigation-right ul > li > a {
    font-size: 14px;
  }
}

#navigation .navigation-left ul > li > a:hover span:after, #navigation .navigation-left ul > li > a:focus span:after,
#navigation .navigation-right ul > li > a:hover span:after,
#navigation .navigation-right ul > li > a:focus span:after {
  width: 100%;
}

#navigation .navigation-left ul > li > a span,
#navigation .navigation-right ul > li > a span {
  position: relative;
  padding: 4px;
}

#navigation .navigation-left ul > li > a span:after,
#navigation .navigation-right ul > li > a span:after {
  content: '';
  display: block;
  height: 2px;
  background-color: #3d71c9;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  transition: width 0.3s ease;
}

#navigation .navigation-logo {
  position: relative;
  width: 16%;
  display: inline-block;
  vertical-align: middle;
}

#navigation .navigation-logo img.logo {
  position: relative;
  width: 100%;
  max-width: 175px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  transform: translate3d(0, 0, 0);
}

@media screen and (max-width: 1380px) {
  #navigation .navigation-logo img.logo {
    max-width: 165px;
  }
}

#navigation-bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: auto;
  padding: 0 5% 25px;
  z-index: 2;
  pointer-events: none;
}

@media screen and (max-width: 1600px) {
  #navigation-bottom {
    padding-left: 4%;
    padding-right: 4%;
  }
}

@media screen and (max-width: 1380px) {
  #navigation-bottom {
    padding-left: 3%;
    padding-right: 3%;
  }
}

#navigation-bottom .navigation-bottom-inner {
  position: relative;
  width: 100%;
}

#navigation-bottom .navigation-bottom-inner .choose-language {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}

#navigation-bottom .navigation-bottom-inner .choose-language.white ul > li {
  color: #fff;
}

#navigation-bottom .navigation-bottom-inner .choose-language ul {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
  font-size: 0;
  pointer-events: auto;
}

#navigation-bottom .navigation-bottom-inner .choose-language ul > li {
  position: relative;
  padding: 2px 8px;
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  transition: color 0.3s ease;
  cursor: pointer;
}

@media screen and (max-width: 1600px) {
  #navigation-bottom .navigation-bottom-inner .choose-language ul > li {
    font-size: 15px;
  }
}

@media screen and (max-width: 1380px) {
  #navigation-bottom .navigation-bottom-inner .choose-language ul > li {
    font-size: 14px;
  }
}

#navigation-bottom .navigation-bottom-inner .choose-language ul > li:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate3d(0, -50%, 0);
  height: 110%;
  width: 1px;
  background-color: #727272;
}

#navigation-bottom .navigation-bottom-inner .choose-language ul > li:before {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #3d71c9;
  transition: width 0.3s ease;
}

#navigation-bottom .navigation-bottom-inner .choose-language ul > li:last-child:after {
  content: none;
}

#navigation-bottom .navigation-bottom-inner .choose-language ul > li:hover:before {
  width: 100%;
}

#navigation-bottom .navigation-bottom-inner .scroll-down-icon-wrapper {
  position: absolute;
  display: inline-block;
  vertical-align: middle;
  left: 50%;
  transform: translate3d(-50%, -20%, 0);
  pointer-events: auto;
}

#navigation-bottom .navigation-bottom-inner .scroll-down-icon-wrapper img.scroll-down-icon {
  position: relative;
  display: block;
  transform: translate3d(0, 0, 0);
  width: 20px;
  margin-bottom: 5px;
}

@media screen and (max-width: 1440px) {
  #navigation-bottom .navigation-bottom-inner .scroll-down-icon-wrapper img.scroll-down-icon {
    width: 16px;
  }
}

#navigation-bottom .navigation-bottom-inner .scroll-down-icon-wrapper img.scroll-down-icon-arrow {
  position: relative;
  display: block;
  width: 10px;
  margin: 0 auto;
  transform: translate3d(0, 0, 0);
}

@media screen and (max-width: 1440px) {
  #navigation-bottom .navigation-bottom-inner .scroll-down-icon-wrapper img.scroll-down-icon-arrow {
    width: 8px;
  }
}

#navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper {
  position: absolute;
  display: inline-block;
  vertical-align: middle;
  right: 0;
  top: 50%;
  transform: translate3d(0, -50%, 0);
}

#navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper.grey ul > li {
  border-right-color: #5c5c5c;
}

#navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper.grey ul > li .fa {
  color: #5c5c5c;
}

#navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper ul {
  padding-left: 0;
  list-style: none;
  font-size: 0;
  pointer-events: auto;
  margin-bottom: 0;
}

#navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper ul > li {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding: 2px 10px;
  border-right: 1px solid #fff;
  line-height: 1;
}

#navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper ul > li:last-child {
  border-right: none;
}

#navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper ul > li .fa {
  font-size: 16px;
  transition: color 0.3s ease;
}

@media screen and (max-width: 1600px) {
  #navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper ul > li .fa {
    font-size: 15px;
  }
}

@media screen and (max-width: 1380px) {
  #navigation-bottom .navigation-bottom-inner .social-media-icon-wrapper ul > li .fa {
    font-size: 14px;
  }
}

.side-menu {
  position: absolute;
  padding: 5% 9% 5% 10%;
  padding: 25px 8%;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: linear-gradient(#eefbff, #c3e4ed);
  transform: translateX(-100%) skewX(-10deg);
  transform-origin: 100% 0;
  transition: transform 0.4s cubic-bezier(0.694, 0, 0.335, 1);
  z-index: 3;
  box-shadow: 10px 10px 74px -9px white;
}

.side-menu.active {
  transform: translateX(0) skewX(-10deg);
}

.side-menu button.close-modal {
  transform: translate3d(0, 0, 0);
  right: 0;
}

.side-menu .flex {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.side-menu .flex .inner {
  position: relative;
  width: 100%;
  max-width: 580px;
  margin-left: auto;
  margin-right: auto;
  transform: skewX(10deg) translate3d(0, 0, 0);
}

.side-menu .flex .inner h2 {
  margin-top: 0;
  margin-bottom: 12%;
  text-align: center;
}

.side-menu .flex .inner .form-group {
  margin-bottom: 10%;
}

@media screen and (max-width: 1600px) {
  .side-menu .flex .inner .form-group {
    margin-bottom: 7%;
  }
}

@media screen and (max-width: 1380px) {
  .side-menu .flex .inner .form-group {
    margin-bottom: 6%;
  }
}

#bottom-section {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.section {
  background: linear-gradient(#c3e4ed, #eefbff);
}

.section .section-content {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  padding: 5%;
}

.section .section-content:before, .section .section-content:after {
  content: '';
  display: table;
}

.section .section-content:after {
  clear: both;
}

.section .section-content.no-padding {
  padding-left: 0;
  padding-right: 0;
}

.section .section-content.no-padding-right {
