}

#about-us img.header-img {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 40%;
  transform: translate3d(0, 0, 0);
}

#about-us .section-title {
  width: 100%;
  max-width: 460px;
}

#about-us h1 {
  color: #535353;
}

button.btn-custom {
  position: relative;
  background-color: #3d71c9;
  font-size: 15px;
  color: #fff;
  border-radius: 0;
  transform-origin: 0 100%;
  padding: 10px 28px;
  border: 1px solid #3d71c9;
  transform: skewX(-20deg);
  overflow: hidden;
  transition: background-color 0.3s ease, color 0.3s ease;
}

@media screen and (max-width: 1380px) {
  button.btn-custom {
    font-size: 14px;
    padding-top: 7px;
    padding-bottom: 7px;
  }
}

button.btn-custom.normal {
  transform: none;
}

button.btn-custom.normal .unskew {
  transform: none;
}

button.btn-custom.no-border {
  border: none;
}

button.btn-custom.no-padding.inverted {
  padding-left: 0;
  padding-right: 0;
  overflow: visible;
}

button.btn-custom.no-padding.inverted span.unskew svg {
  overflow: visible;
}

button.btn-custom.no-padding.inverted span.unskew svg * {
  transition: all 0.3s cubic-bezier(0.694, 0, 0.335, 1);
}

button.btn-custom.no-padding.inverted:hover, button.btn-custom.no-padding.inverted:focus, button.btn-custom.no-padding.inverted:active, button.btn-custom.no-padding.inverted.active {
  color: #727272;
  background-color: #fff;
}

button.btn-custom.no-padding.inverted:hover span.unskew svg *, button.btn-custom.no-padding.inverted:focus span.unskew svg *, button.btn-custom.no-padding.inverted:active span.unskew svg *, button.btn-custom.no-padding.inverted.active span.unskew svg * {
  fill: #3d71c9;
  transform: translateX(20px);
}

button.btn-custom.no-padding.inverted:before {
  content: '';
  display: block;
}

button.btn-custom.inverted {
  background-color: #fff;
  color: #727272;
}

button.btn-custom.inverted span.unskew svg * {
  fill: #3d71c9;
}

button.btn-custom.inverted:hover, button.btn-custom.inverted:focus, button.btn-custom.inverted:active, button.btn-custom.inverted.active {
  color: #fff;
  background-color: #3d71c9;
}

button.btn-custom.inverted:hover span.unskew svg *, button.btn-custom.inverted:focus span.unskew svg *, button.btn-custom.inverted:active span.unskew svg *, button.btn-custom.inverted.active span.unskew svg * {
  fill: #fff;
}

button.btn-custom.transparent {
  background-color: transparent;
  color: #fff;
}

button.btn-custom:hover, button.btn-custom:focus, button.btn-custom:active, button.btn-custom.active {
  color: #727272;
  background-color: #fff;
}

button.btn-custom:hover span.unskew svg *, button.btn-custom:focus span.unskew svg *, button.btn-custom:active span.unskew svg *, button.btn-custom.active span.unskew svg * {
  fill: #3d71c9;
}

button.btn-custom span.unskew {
  display: block;
  transform: skewX(20deg);
}

button.btn-custom span.unskew span.text {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}

button.btn-custom span.unskew svg {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
}

button.btn-custom span.unskew svg * {
  transition: fill 0.3s ease;
}

button.btn-custom.min-width {
  min-width: 130px;
}

button.btn-custom.smaller-padding {
  padding-top: 6px;
  padding-bottom: 6px;
}

button.close-modal {
  position: absolute;
  top: 0;
  right: 0;
  padding: 20px 10px;
  background-color: #3d71c9;
  z-index: 2;
}

button.close-modal.inverted {
  background-color: #fff;
}

button.close-modal.inverted .close-icon span.stripe {
  background-color: #3d71c9;
}

button.close-modal .close-icon {
  position: relative;
  transform: rotateZ(45deg);
}

button.close-modal .close-icon span.stripe {
  position: relative;
  display: block;
  height: 2px;
  width: 22px;
  background-color: #fff;
}

button.close-modal .close-icon span.stripe:last-child {
  position: absolute;
  top: -10px;
  left: 10px;
  width: 2px;
  height: 22px;
}

#news-and-gallery .news-and-gallery-thumbnail {
  position: relative;
  width: 25%;
  float: left;
  height: 50vh;
  transform: skewX(-10deg);
  overflow: hidden;
  backface-visibility: initial;
  image-rendering: crisp-edges;
  transform-origin: 0 100%;
}

#news-and-gallery .news-and-gallery-thumbnail.bottom {
  transform: skewX(10deg);
  transform-origin: 0 0;
}

#news-and-gallery .news-and-gallery-thumbnail.bottom .inner {
  transform: skewX(-10deg);
