Slider navigation

-------------------------------- */
.cd-slider-controls {
  position: absolute;
  bottom: 15%;
  left: 50%;
  right: auto;
  transform: translateX(-50%);
  z-index: 3;
  text-align: center;
  width: 90%;
  padding-left: 0;
}

.cd-slider-controls::after {
  clear: both;
  content: "";
  display: table;
}

.cd-slider-controls li {
  display: inline-block;
  margin-right: 10px;
}

.cd-slider-controls li:last-of-type {
  margin-right: 0;
}

.cd-slider-controls li.selected a {
