svg cover layer

-------------------------------- */
.cd-svg-cover {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
}

.cd-svg-cover path {
  fill: #3d71c9;
}

.cd-svg-cover.is-animating {
  z-index: 4;
  opacity: 1;
  transition: opacity 0.6s;
}

