.scroll-down-animation {
  animation: scroll-down-animation 1.5s linear infinite;
}

@keyframes scroll-down-animation {
  0% {
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translate3d(0, 10px, 0);
    opacity: 0;
  }
}

